import React, { useEffect, useState } from 'react';
import { Grid, TextField, Button } from "@material-ui/core";
import { makeStyles, createStyles } from '@material-ui/core/styles';
import { useParams } from "react-router-dom";

import CustomizedBreadcrumbs from '../../components/Breadcrumbs/index';
import callAuthAPI from '../../utils/CallAuthAPI';
import { ACCESS_TOKEN } from '../../constant/variables';
import {convertDateToString} from '../../services/date'

const useStyles = makeStyles((theme) =>
  createStyles({
    root: {
      backgroundColor: "#ffffff",
      padding: theme.spacing(2),
      marginBottom: theme.spacing(2),
    },
    emailInput: {
      width: theme.spacing(40),
    },
    btn: {
      marginLeft: theme.spacing(2),
      marginTop: theme.spacing(1),
    }
  }),
);



const UserDetail = () => {
  const [data, setData] = useState({});
  const [refesh, setRefesh] = useState(true);
  const { id } = useParams();
  useEffect(() => {
    callAuthAPI(`users/${id}`, 'GET', {}, JSON.parse(localStorage.getItem(ACCESS_TOKEN))).then((res) => {
      console.log(res.data);
      setData(res.data);
    })
  }, [id,refesh])
  const classes = useStyles();

  const handleActiveUser = (id) => {
    console.log('Active id:' + id);
    callAuthAPI('users/activeuser', 'POST', { id: id}, JSON.parse(localStorage.getItem(ACCESS_TOKEN))).then((res) => {
      console.log(res.data);
      setRefesh(!refesh);
    });
  }
  return (
    <>
      <Grid container spacing={2}>
        <Grid item xs={12}>
          <CustomizedBreadcrumbs nextLinks={[]} finalLink={{ label: "Xem thng tin ngi dng", path: '/user/' }} />
        </Grid>
        <Grid item xs={12} className={classes.root}>
          <h1>Thng tin ngi dng </h1>
          <h3>Thng tin c nhn</h3>
          <Grid container spacing={5} alignItems="center">
            <Grid item>
              <TextField variant="filled" label='H tn' placeholder="H tn" value={data.name ? data.name : ''} flexGrow={1} InputProps={{ readOnly: true, }} />
            </Grid>
            <Grid item>
              <TextField variant="filled" label='Tn ti khon' placeholder="Tn ti khon" value={data.username ? data.username : ''} InputProps={{ readOnly: true, }} />
            </Grid>
            <Grid item>
              <TextField variant="filled" className={classes.emailInput} label='Email' placeholder="Email" value={data.email ? data.email : ''} InputProps={{ readOnly: true, }} />
            </Grid>
          </Grid>
          <h3>Lch s chi</h3>
          <Grid container spacing={5} alignItems="center">
            <Grid item>
              <TextField variant="filled" label='S cp' placeholder="S cp" value={data.cup ? data.cup : 0} InputProps={{ readOnly: true, }} />
            </Grid>
            <Grid item>
              <TextField variant="filled" label='S trn  chi' placeholder="S trn  chi" value={data.countMatch ? data.countMatch : 0} InputProps={{ readOnly: true, }} />
            </Grid>
            <Grid item>
              <TextField variant="filled" label='Rank' placeholder="Rank" value={data.rank ? data.rank : ''} InputProps={{ readOnly: true, }} />
            </Grid>
            <Grid item>
              <TextField variant="filled" label='T l thng' placeholder="T l thng" value={data.winRatio ? data.winRatio : ''} InputProps={{ readOnly: true, }} />
            </Grid>
          </Grid>
          <h3>Thng tin ti khon</h3>
          <Grid container spacing={5} alignItems="center">
            <Grid item>
              <TextField variant="filled" label='Trng thi ti khon' placeholder="Trng thi ti khon" value={data.status ? 'ang m' : ' kha'} InputProps={{ readOnly: true, }} />
            </Grid>
            <Grid item>
              <TextField variant="filled" label='Vai tr' placeholder="Vai tr" value={data.role ? data.role === 2 ? 'Admin' : 'User' : ''} InputProps={{ readOnly: true, }} />
            </Grid>
            <Grid item>
              <TextField variant="filled" label='Trng thi kch hot ti khon' placeholder="Trng thi kch hot ti khon" value={data.isActive ? ' kch hot' : 'Cha kch hot'} InputProps={{ readOnly: true, }} />
              {
                data.isActive===false ? <Button onClick={data.username ? () => handleActiveUser(id) : null} className={classes.btn} variant="contained" color="primary">
                  Kch hot ti khon
              </Button>:null
              }
            </Grid>
            <Grid item>
              <TextField variant="filled" label='Ngy to' placeholder="Ngy to" value={data.dateCreate ? convertDateToString(data.dateCreate) : ''} InputProps={{ readOnly: true, }} />
            </Grid>
          </Grid>
        </Grid>
      </Grid>
    </>
  )
}
export default UserDetail;